.Card{
    margin: 30px;
    width: 300px;
    background-color:#dafbf6;
    padding: 30px;
    border-radius: 20px;
    box-shadow: 8px 5px 5px rgb(138, 137, 137);
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}
.Card img {
    height:230px;
}

.Card:hover{
    transform: scale(1.1);
}